<template>
  <div class="comic-card">
    <div class="label" v-if="comic.event !== '' && comic.event !== null && comic.event !== 'Нет' ">{{ comic.event }}</div>
    <h3 class="comic-card__title">{{ comic.title }}</h3>
    <h4 class="comic-card__title-ru">{{ comic.title_ru }}</h4>
    <img class="comic-card__main-pic" :src="comic.coverImage" :alt="comic.title"  />
    <p><b>Издательство:</b>
      <template v-if="comic.publisher === 'Marvel'">
        <img class="comic-card__publisher" src="/images/marvel.png" alt="Marvel">
      </template>
      <template v-else-if="comic.publisher === 'DC Comics'">
        <img class="comic-card__publisher" src="/images/dc.png" alt="DC Comics">
      </template>
      <template v-else>
        {{ comic.publisher }}
      </template>
    </p>
    <div><b>Том:</b> {{ comic.volume }}</div>
    <div><b>Выпусков:</b> {{ comic.issues }}</div>
      <div class="rating-block">
        <span><b>Рейтинг:</b> {{ comic.rating }} из 100</span>
        <svg width="32" height="32" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32">
          <path
            d="M31.547 12a.848.848 0 00-.677-.577l-9.427-1.376-4.224-8.532a.847.847 0 00-1.516 0l-4.218 8.534-9.427 1.355a.847.847 0 00-.467 1.467l6.823 6.664-1.612 9.375a.847.847 0 001.23.893l8.428-4.434 8.432 4.432a.847.847 0 001.229-.894l-1.615-9.373 6.822-6.665a.845.845 0 00.214-.869z" />
        </svg>
      </div>

    <p><b>Год:</b> {{ comic.year }}</p>
    <p class="comic-card__description" v-html="comic.summary"></p>

    <div :id="galleryID" class="comic-card__gallery" v-if="comic.additionalImages && comic.additionalImages.length > 0">
      <a
        v-for="(image, index) in comic.additionalImages"
        :key="index"
        :href="image"
        target="_blank"
        rel="noreferrer"   
      >
        <img :src="image" alt="Доп. фото" style="margin-right: 5px" />
      </a>
    </div>
    <div class="button-block">
    <router-link :to="{ name: 'comic-detail', params: { id: comic.id } }" class="button button--accent">
      Подробнее
    </router-link>
    <a class="button button--accent" :href=comic.link target="_blank">Читать</a>
  </div>
  </div>
</template>

<script>
import PhotoSwipeLightbox from 'photoswipe/lightbox';
import 'photoswipe/style.css';

export default {
  name: "ComicCard",
  props: {
    comic: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      galleryID: `gallery-${Math.random().toString(36).substring(2, 15)}`,
      lightbox: null,
    };
  },
  mounted() {
    if (this.comic.additionalImages && this.comic.additionalImages.length) {
      this.lightbox = new PhotoSwipeLightbox({
        gallery: '#' + this.galleryID,
        children: 'a',
        pswpModule: () => import('photoswipe'),
        loop: true,
        zoomTitle: 'Zoom',
        zoomEl: true,
        wheelToZoom: true,
        pinchToClose: true,
              	closeOnScroll: false,
                closeOnVerticalDrag: true,
                bgOpacity: 1,
                preventSwiping: false,
                showAnimationDuration: 0,
                hideAnimationDuration: 0,
                animateTransitions: true,

                closeEl: true,
                captionEl: true,
                fullscreenEl: true,
                
                shareEl: true,
                downloadEl: true,
                counterEl: true,
                arrowEl: true,
                preloaderEl: true,
                closeOnOutsideClick: false,
                tapToClose: false,
                tapToToggleControls: false,
      });
      this.lightbox.init();
    }
  },
  unmounted() {
    if (this.lightbox) {
      this.lightbox.destroy();
      this.lightbox = null;
    }
  },
  methods: {
    async getImageSize(imageUrl) {
      return new Promise((resolve) => {
        const img = new Image();
        img.onload = () => {
          resolve({ w: img.width, h: img.height });
        };
        img.onerror = () => {
          resolve({ w: 0, h: 0 }); //  В случае ошибки, возвращаем 0
        };
        img.src = imageUrl;
      });
    },
    async openPhotoSwipe(index = 0) {
      const imagesWithSizes = await Promise.all(
        (this.comic.additionalImages || []).map(async (url) => {
          const size = await this.getImageSize(url);
          return {
            src: url,
            w: size.w,
            h: size.h,
          };
        })
      );
      this.photoSwipeImages = imagesWithSizes;
      this.photoSwipeOptions.index = index;
      this.lightbox.loadAndOpen(index);
    },
  },
};
</script>

