<template>
  <div class="comic-form">
    <h2>{{ comic ? 'Редактировать комикс' : 'Добавить комикс' }}</h2>
    <form @submit.prevent="onSubmit">
      <div class="form-group">
        <label for="title">Название:</label>
        <input type="text" id="title" v-model="form.title" required />
      </div>

      <div>
      <label for="title_ru">Название (рус):</label>
      <input type="text" id="title_ru" v-model="form.title_ru">
    </div>

      <div class="form-group">
        <label for="publisher">Издательство:</label>
        <input type="text" id="publisher" v-model="form.publisher" required />
      </div>

      <div class="form-group">
        <label for="characters">Персонажи (через запятую):</label>
        <input type="text" id="characters" v-model="form.charactersString" @blur="splitCharacters" />
      </div>

      <div class="form-group">
        <label for="year">Год:</label>
        <input type="number" id="year" v-model.number="form.year" />
      </div>

      <div class="form-group">
        <label for="startDate">Дата начала:</label>
        <input type="date" id="startDate" v-model="form.startDate" />
      </div>

      <div class="form-group">
        <label for="endDate">Дата завершения:</label>
        <input type="date" id="endDate" v-model="form.endDate" />
      </div>

      <div class="form-group">
        <label for="volume">Том:</label>
        <input type="number" id="volume" v-model="form.volume" />
      </div>

      <div class="form-group">
        <label for="issues">Выпуски:</label>
        <input type="number" id="issues" v-model.number="form.issues" />
      </div>

      <div class="form-group">
        <label for="readTo">Прочитано до выпуска:</label>
        <input type="number" id="readTo" v-model.number="form.readTo" />
      </div>

      <div class="form-group">
        <label for="genre">Жанр:</label>
        <input type="text" id="genre" v-model="form.genre" />
      </div>

      <div class="form-group">
        <label for="event">Событие:</label>
        <input type="text" id="event" v-model="form.event" />
      </div>

      <div class="form-group">
        <label for="status">Статус:</label>
        <select id="status" v-model="form.status">
          <option value="продолжается">Продолжается</option>
          <option value="закончен">Закончен</option>
        </select>
      </div>

      <div class="form-group">
        <label for="artists">Художники (через запятую):</label>
        <input type="text" id="artists" v-model="form.artistsString" @blur="splitArtists" />
      </div>

      <div class="form-group">
        <label for="summary">Краткое описание:</label>
        <textarea id="summary" v-model="form.summary"></textarea>
      </div>

      <div class="form-group">
        <label for="notes">Заметки:</label>
        <textarea id="notes" v-model="form.notes"></textarea>
      </div>

      <div class="form-group">
        <label for="rating">Рейтинг (1-100):</label>
        <input type="number" id="rating" v-model.number="form.rating" min="1" max="100" />
      </div>

      <div class="form-group">
        <label for="artRating">Рейтинг рисовки (1-100):</label>
        <input type="number" id="artRating" v-model.number="form.artRating" min="1" max="100" />
      </div>

      <div class="form-group">
        <label for="coverImage">Главное фото:</label>
        <input type="file" id="coverImage" @change="handleCoverImageChange" />
        <img v-if="form.coverImage" :src="form.coverImage" alt="Обложка" width="100" />
      </div>

      <div class="form-group">
      <label for="additionalImages">Дополнительные фото:</label>
      <input type="file" id="additionalImages" multiple @change="handleAdditionalImagesChange" />
      <div :id="galleryID" class="comic-card__gallery" v-if="form.additionalImages.length > 0">
        <a
          v-for="(image, index) in form.additionalImages"
          :key="index"
          :href="image"
          target="_blank"
          rel="noreferrer"
        >
          <img :src="image" alt="Доп. фото" width="100" style="margin-right: 5px; cursor: pointer" />
        </a>
      </div>
    </div>

      <div class="form-group">
        <label for="link">Ссылка:</label>
        <input type="text" id="link" v-model="form.link" />
      </div>

      <div class="form-group">
        <label for="isFavorite">Интересное:</label>
        <input type="checkbox" id="isFavorite" v-model="form.isFavorite" />
      </div>

      <button type="submit">Сохранить</button>
    </form>




  </div>
</template>

<script>
import axios from "axios";
import PhotoSwipeLightbox from 'photoswipe/lightbox';
import 'photoswipe/style.css';

export default {
  name: "ComicForm",
  props: {
    comic: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      form: {
        title: this.comic ? this.comic.title : "",
        title_ru: this.comic ? this.comic.title_ru : "",
        publisher: this.comic ? this.comic.publisher : "",
        characters: this.comic ? [...this.comic.characters] : [], // Copy the array
        year: this.comic ? this.comic.year : null,
        startDate: this.comic ? this.comic.startDate : null,
        endDate: this.comic ? this.comic.endDate : null,
        volume: this.comic ? this.comic.volume : null,
        issues: this.comic ? this.comic.issues : null,
        readTo: this.comic ? this.comic.readTo : null,
        genre: this.comic ? this.comic.genre : "",
        event: this.comic ? this.comic.event : "",
        status: this.comic ? this.comic.status : "продолжается",
        artists: this.comic ? [...this.comic.artists] : [], // Copy the array
        summary: this.comic ? this.comic.summary : "",
        notes: this.comic ? this.comic.notes : "",
        rating: this.comic ? this.comic.rating : null,
        artRating: this.comic ? this.comic.artRating : null,
        coverImage: this.comic ? this.comic.coverImage : "",
        additionalImages: this.comic ? [...this.comic.additionalImages] : [], // Copy the array
        link: this.comic ? this.comic.link : "",
        isFavorite: this.comic ? this.comic.isFavorite : false,
      },
      galleryID: `gallery-${Math.random().toString(36).substring(2, 15)}`,
      lightbox: null,
    };
  },
  computed: {
    formCharacters: {
      get() {
        return this.form.characters.join(', ');
      },
      set(value) {
        this.form.characters = value.split(',').map(item => item.trim());
      }
    },
     formArtists: {
      get() {
        return this.form.artists.join(', ');
      },
      set(value) {
        this.form.artists = value.split(',').map(item => item.trim());
      }
    }
  },
  mounted() {
    if (this.form.additionalImages.length > 0) {
      this.lightbox = new PhotoSwipeLightbox({
        gallery: '#' + this.galleryID,
        children: 'a',
        pswpModule: () => import('photoswipe'),
        loop: true,
        zoomTitle: 'Zoom',
        zoomEl: true,
        wheelToZoom: true,
        pinchToClose: true,
        closeOnScroll: false,
        closeOnVerticalDrag: true,
        bgOpacity: 1,
        preventSwiping: false,
        showAnimationDuration: 0,
        hideAnimationDuration: 0,
        animateTransitions: true,
        closeEl: true,
        captionEl: true,
        fullscreenEl: true,
        shareEl: true,
        downloadEl: true,
        counterEl: true,
        arrowEl: true,
        preloaderEl: true,
        closeOnOutsideClick: false,
        tapToClose: false,
        tapToToggleControls: false,
      });

      this.lightbox.init();
    }
  },
  beforeUnmount() {
    if (this.lightbox) {
      this.lightbox.destroy();
      this.lightbox = null;
    }
  },
  methods: {
    async getImageSize(imageUrl) {
      return new Promise((resolve) => {
        const img = new Image();
        img.onload = () => {
          resolve({ w: img.width, h: img.height });
        };
        img.onerror = () => {
          resolve({ w: 800, h: 600 }); // Размер по умолчанию при ошибке
        };
        img.src = imageUrl;
      });
    },

    async handleAdditionalImagesChange(event) {
      const files = event.target.files;

      if (files && files.length > 0) {
        this.form.additionalImages = [];

        for (let i = 0; i < files.length; i++) {
          const reader = new FileReader();
          reader.onload = (e) => {
            this.form.additionalImages.push(e.target.result);
          };
          reader.readAsDataURL(files[i]);
        }

        // Пересоздаем галерею после добавления новых картинок
        await this.$nextTick();

        if (this.lightbox) {
          this.lightbox.destroy();
        }

        this.lightbox = new PhotoSwipeLightbox({
          gallery: '#' + this.galleryID,
          children: 'a',
          pswpModule: () => import('photoswipe'),
          loop: true,
          zoomEl: true,
          pinchToClose: true,
          closeOnScroll: false,
          captionEl: true,
          counterEl: true,
        });

        this.lightbox.init();
      }
    },





    


    formMainImg(inputString)  {

      function normalizeString(inputString) {
  if (typeof inputString !== 'string') {
    console.error("Input must be a string.");
    return "";
  }

  // Карта для транслитерации русских букв
  const translitMap = {
    'а': 'a', 'б': 'b', 'в': 'v', 'г': 'g', 'д': 'd', 'е': 'e', 'ё': 'yo', 'ж': 'zh',
    'з': 'z', 'и': 'i', 'й': 'j', 'к': 'k', 'л': 'l', 'м': 'm', 'н': 'n', 'о': 'o',
    'п': 'p', 'р': 'r', 'с': 's', 'т': 't', 'у': 'u', 'ф': 'f', 'х': 'h', 'ц': 'c',
    'ч': 'ch', 'ш': 'sh', 'щ': 'shh', 'ъ': '0', 'ы': 'y', 'ь': '0', 'э': 'e', 'ю': 'yu',
    'я': 'ya',
    'А': 'A', 'Б': 'B', 'В': 'V', 'Г': 'G', 'Д': 'D', 'Е': 'E', 'Ё': 'Yo', 'Ж': 'Zh',
    'З': 'Z', 'И': 'I', 'Й': 'J', 'К': 'K', 'Л': 'L', 'М': 'M', 'Н': 'N', 'О': 'O',
    'П': 'P', 'Р': 'R', 'С': 'S', 'Т': 'T', 'У': 'U', 'Ф': 'F', 'Х': 'H', 'Ц': 'C',
    'Ч': 'Ch', 'Ш': 'Sh', 'Щ': 'Shh', 'Ъ': '0', 'Ы': 'Y', 'Ь': '0', 'Э': 'E', 'Ю': 'Yu',
    'Я': 'Ya'
    // Убрали '!', '?' отсюда, так как они будут заменяться на тире ниже
  };

  let result = "";
  for (let i = 0; i < inputString.length; i++) {
    const char = inputString[i];
    result += translitMap[char] || char;
  }

  // Заменяем пробелы, точки, запятые, точка с запятой, двоеточия,
  // кавычки, скобки и другие указанные символы на тире
  // \s - пробелы (включая табы и новые строки)
  // \. - точка
  // , - запятая
  // ; - точка с запятой
  // : - двоеточие
  // ['"“”‘’«»] - все виды кавычек
  // [(){}\[\]] - все виды скобок
  // [!&?#№] - добавленные символы для замены
  result = result.replace(/[\s.,;:'"“”‘’«»(){}\[\]!&?#№]/g, '-');

  // Удаляем последовательные тире, если они образовались
  result = result.replace(/-+/g, '-');

  // Удаляем тире в начале и в конце строки, если они есть
  result = result.replace(/^-+|-+$/g, '');  

  // Приводим к нижнему регистру
  return result.toLowerCase();
}



        return normalizeString(inputString); 
    },
    async onSubmit() {
      if (!this.validateForm()) {
        return;
      }

      try {
        if (this.comic) {
          // Редактирование существующего комикса
          await axios.put(`http://localhost:3000/comics/${this.comic.id}`, this.form);
          this.$emit("comic-updated"); // Испускаем событие comic-updated
        } else {
          if(this.form.title) {
            this.form.coverImage = "/images/" + this.formMainImg(this.form.title) + "/001.jpg";
            this.form.additionalImages = [];
            for (let i = 1; i <= 6; i++) {
              this.form.additionalImages.push("/images/" + this.formMainImg(this.form.title) + `/00${i}.jpg`);
            }
          }
          
          // Добавление нового комикса
          const response = await axios.post("http://localhost:3000/comics", this.form);
          const newComic = response.data; // Получаем созданный объект из ответа
          console.log("Комикс добавлен с ID:", newComic.id);
          this.$router.push({ name: 'comic-detail', params: { id: newComic.id } });
        }
      } catch (error) {
        console.error("Ошибка при сохранении комикса:", error);
      }
    },
    validateForm() {
      if (!this.form.title || !this.form.publisher || !this.form.year) {
        alert("Пожалуйста, заполните обязательные поля: Название, Издательство, Год");
        return false;
      }
      if (this.form.year && (this.form.year < 1900 || this.form.year > new Date().getFullYear())) {
        alert("Некорректный год");
        return false;
      }
      if (this.form.rating && (this.form.rating < 1 || this.form.rating > 100)) {
        alert("Рейтинг должен быть от 1 до 100");
        return false;
      }
      if (this.form.artRating && (this.form.artRating < 1 || this.form.artRating > 100)) {
        alert("Рейтинг рисовки должен быть от 1 до 100");
        return false;
      }
      return true;
    },
    splitCharacters() {
      this.form.characters = this.formCharacters.split(',').map(item => item.trim());
    },
    splitArtists() {
       this.form.artists = this.formArtists.split(',').map(item => item.trim());
    },
    handleCoverImageChange(event) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.form.coverImage = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
    handleAdditionalImagesChange(event) {
      const files = event.target.files;
      if (files) {
        this.form.additionalImages = [];
        for (let i = 0; i < files.length; i++) {
          const reader = new FileReader();
          reader.onload = (e) => {
            this.form.additionalImages.push(e.target.result);
          };
          reader.readAsDataURL(files[i]);
        }
      }
    },
  },
};
</script>

<style scoped>
.comic-form {
  padding: 20px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 10px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="number"],
textarea,
select {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
}

button[type="submit"] {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

.form-group img {
  cursor: pointer;
}

.comic-card__gallery {
  display: flex !important;
  flex-wrap: wrap;
  gap: 5px;
}

.comic-card__gallery a {
  flex: 0 0 31%;
  width: 31%;
}
</style>