<template>
  <div class="random-comics">
    <h2>Рандомные комиксы</h2>
    <div class="filters">
      <label for="publisher">Издательство:</label>
      <select id="publisher" v-model="selectedPublisher" @change="updateRandomComics">
        <option value="">Все</option>
        <option v-for="publisher in publishers" :key="publisher.name" :value="publisher.name">
          {{ publisher.name }} ({{ publisher.count }})
        </option>
      </select>
      <button @click="updateRandomComics">Обновить</button>
    </div>

    <div class="comic-list">
      <comic-card v-for="comic in randomComicsList" :key="comic.id" :comic="comic" />
    </div>
  </div>
</template>

<script>
import ComicCard from "./ComicCard.vue";

export default {
  name: "RandomComics",
  components: {
    ComicCard,
  },
  props: {
    comics: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selectedPublisher: "",
      randomComicsList: [],
    };
  },
  computed: {
    publishers() {
      return this.comics
        .map((comic) => comic.publisher)
        .reduce((acc, publisher) => {
          const existing = acc.find((item) => item.name === publisher);
          if (existing) {
            existing.count++;
          } else {
            acc.push({ name: publisher, count: 1 });
          }
          return acc;
        }, [])
        .sort((a, b) => a.name.localeCompare(b.name));
    },
    filteredComics() {
      if (!this.selectedPublisher) {
        return this.comics;
      }
      return this.comics.filter((comic) => comic.publisher === this.selectedPublisher);
    },
  },
  methods: {
    getRandomComics(comics, count) {
      const availableComics = comics;
      const randomComics = [];
      const actualCount = Math.min(count, availableComics.length);

      if (availableComics.length === 0) {
        return [];
      }

      const indices = new Set();
      while (indices.size < actualCount) {
        const randomIndex = Math.floor(Math.random() * availableComics.length);
        indices.add(randomIndex);
      }

      indices.forEach((index) => {
        randomComics.push(availableComics[index]);
      });

      return randomComics;
    },
    updateRandomComics() {
      this.randomComicsList = this.getRandomComics(this.filteredComics, 4);
    },
  },
  watch: {
    comics: {
      handler() {
        this.updateRandomComics();
      },
      deep: true, //  Важно для отслеживания изменений в массиве
    },
  },
};
</script>

<style scoped lang="scss">
.random-comics {
  margin-bottom: 20px;
  border: 1px solid #ccc;
  padding: 10px;
}

.comic {
  &-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
    font-family: 'Montserrat', 'Arial', sans-serif;
    font-size: 18px;
    line-height: 28px;
    color: #25292d;

    justify-content: space-around;

    & .comic-card {
      width: auto;
      min-height: 400px;
      box-shadow: rgba(34, 39, 47, 0.08) 0px 16px 72px 0px;
      border: 1px solid rgb(235, 234, 234);
      background-color: rgb(255, 255, 255);
      // margin-right: 30px;
      padding: 24px 24px 34px;
      text-decoration: none;
      border-radius: 24px;

      & p {
        font-size: 16px;
        line-height: 1.3;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 10;
        -webkit-box-orient: vertical;

        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }

      &__gallery {
        display: flex !important;
        overflow: hidden;

        & img {
          flex: 0 0 32%;
          width: 32%;
          object-fit: cover;
          margin-right: 2px;
          cursor: pointer;
        }
      }

      &:hover {
        box-shadow: rgba(34, 39, 47, 0.11) 0px 16px 72px 0px;
      }

      &__title {
        font-family: 'Montserrat', 'Arial', sans-serif;
        font-size: 24px;
        font-weight: 700;
        color: #25292d;
        margin-bottom: 10px;
      }
    }
  }

}

.filters {
  display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin-bottom: 30px;
    max-width: 600px;
}
</style>