<template>
  <div id="app">
    <header>
      <h1>Каталогизатор комиксов</h1>
      <nav>
        <router-link to="/">Список комиксов</router-link>
      </nav>
    </header>
    <main>
      <router-view />
    </main>
    <footer>
      <p>&copy; 2023 Мой Каталогизатор</p>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted() {
    window.addEventListener('load', () => {
      console.log('Вся страница загружена (включая изображения)');
      this.yourCustomFunction();
    });
  },
  methods: {
    yourCustomFunction() {

    // Preloader страницы
    const preloader = document.querySelector('.preloader');
    const content = document.querySelector('#app');
    if (preloader && content) {
            // Скрываем прелоадер
            preloader.style.opacity = '0';
            preloader.style.visibility = 'hidden';

            // Показываем контент
            content.style.display = 'block';

            // Удаляем элемент из DOM
            preloader.remove();
    }
    console.log('Выполняется пользовательская функция после полной загрузки');

    }
  }
}
</script>

