<template>
  <div class="comic-list">
    
    <RandomComics :comics="comics" />
    
    
    <h2>
      Список комиксов ({{ comics.length }})
    </h2>

    <div class="search">
        <h3 class="search__title">Поиск</h3>
        <div class="search__input-search">
          <input class="search__field-search" :class="{ 'search-applied': isSearchApplied }" type="search" v-model="searchQuery" placeholder="Введите текст для поиска" />
          <button @click="applySearch" class="button button--accent">Поиск</button>
        </div>        
        <span v-if="isSearchApplied" style="color: green; margin-left: 10px;">✔ Поиск применён</span>
        <div class="search__list">
          <input class="search__field" type="radio" v-model="searchField" value="title" id="title"><label class="search__label" for="title">Заголовок (ENG)</label>
          <input class="search__field" type="radio" v-model="searchField" value="title_ru" id="title-ru"><label class="search__label" for="title-ru">Заголовок (RU)</label>
          <input class="search__field" type="radio" v-model="searchField" value="summary" id="summary"><label class="search__label"for="summary">Описание</label>
        </div>        
      </div> 

      <hr>

      <div class="filters">
        <h3>Фильтр:</h3>
    <p v-if="selectedPublisher || selectedCharacter || selectedYear || selectedArtist">
      Фильтр:
      <span v-if="selectedPublisher">Издательство {{ selectedPublisher }}</span>
      <span v-if="selectedPublisher && (selectedCharacter || selectedYear || selectedArtist)">, </span> 
      <span v-if="selectedCharacter">Персонаж {{ selectedCharacter }}</span>
      <span v-if="selectedCharacter && (selectedYear || selectedArtist)">, </span>
      <span v-if="selectedYear">Год {{ selectedYear }}</span>
      <span v-if="selectedYear && selectedArtist">, </span>
      <span v-if="selectedArtist">Художник {{ selectedArtist }}</span>
      ({{ filteredComics.length }})
    </p>

<div class="filter-block">

    <div class="filter">
      <label for="publisher">Издательство:</label>
      <select id="publisher" v-model="selectedPublisher">
        <option value="">Все</option>
        <option v-for="publisher in publishers" :key="publisher.name" :value="publisher.name">
          {{ publisher.name }} ({{ publisher.count }})
        </option>
      </select>
    </div>

    <div class="filter">
      <label for="artist">Художник:</label>
      <select id="artist" v-model="selectedArtist">
        <option value="">Все</option>
        <option v-for="artist in artists" :key="artist.name" :value="artist.name">
          {{ artist.name }} ({{ artist.count }})
        </option>
      </select>
    </div>

    <div class="filter">
      <label for="character">Персонаж:</label>
      <select id="character" v-model="selectedCharacter">
        <option value="">Все</option>
        <option v-for="character in characters" :key="character.name" :value="character.name">
          {{ character.name }} ({{ character.count }})
        </option>
      </select>
    </div>

    <div class="filter">
      <label for="year">Год:</label>
      <select id="year" v-model="selectedYear">
        <option value="">Все</option>
        <option v-for="year in years" :key="year.name" :value="String(year.name)">
          {{ year.name }} ({{ year.count }})
        </option>
      </select>
    </div>
  </div>
    
  </div>
  <hr>
  <div>

    <h3>Сортировка:</h3>
    <div class="sort">
      <button @click="toggleSort('year')" class="sort-button">
        Год <span v-html="yearSortArrow"></span>
      </button>
      <button @click="toggleSort('rating')" class="sort-button">
        Рейтинг <span v-html="ratingSortArrow"></span>
      </button>
      <button @click="resetSort" class="reset-button">Сбросить</button>
    </div>

  </div>

    <div class="comics-block">
      <comic-card v-for="comic in paginatedComics" :key="comic.id" :comic="comic" />
    </div>


    <div class="pagination">
      <button @click="goToPreviousPage" :disabled="currentPage === 1" class="button button--accent">
        Предыдущая
      </button>

      <input  type="number" v-model.number="currentPageInput" @blur="goToPageFromInput" min="1" :max="totalPages" />
      <span>/ {{ totalPages }}</span>      

      <button @click="goToNextPage" :disabled="currentPage === totalPages" class="button button--accent">
        Следующая
      </button>
    </div>
  </div>
</template>

<script>
import ComicCard from "./ComicCard.vue";
import RandomComics from "./RandomComics.vue";
import axios from "axios";

const ITEMS_PER_PAGE = 8;

export default {
  name: "ComicList",
  components: {
    ComicCard,
    RandomComics,
  },
  data() {
    return {
      comics: [],
      currentPage: 1,
      currentPageInput: 1,
      selectedPublisher: "",
      selectedCharacter: "",
      selectedYear: "",
      selectedArtist: "",
      sortField: null,
      sortOrder: null,

      // Данные для поиска
      searchQuery: "",
      searchField: "title",
      isSearchApplied: false,
      isSearchAppliedClass: false,
    };
  },
  mounted() {
    this.fetchComics();
  },
  computed: {
    totalPages() {
      return Math.ceil(this.sortedAndFilteredComics.length / ITEMS_PER_PAGE);
    },
    paginatedComics() {
      const startIndex = (this.currentPage - 1) * ITEMS_PER_PAGE;
      const endIndex = startIndex + ITEMS_PER_PAGE;
      return this.sortedAndFilteredComics.slice(startIndex, endIndex);
    },
    publishers() {
      return this.comics
        .map((comic) => comic.publisher)
        .reduce((acc, publisher) => {
          const existing = acc.find((item) => item.name === publisher);
          if (existing) {
            existing.count++;
          } else {
            acc.push({ name: publisher, count: 1 });
          }
          return acc;
        }, [])
        .sort((a, b) => a.name.localeCompare(b.name));
    },
    characters() {
      let filteredComics = this.comics;
      if (this.selectedPublisher) {
        filteredComics = filteredComics.filter(
          (comic) => comic.publisher === this.selectedPublisher
        );
      }
      if (this.selectedArtist) {
        filteredComics = filteredComics.filter(
          (comic) => comic.artists && comic.artists.includes(this.selectedArtist)
        );
      }
      return filteredComics
        .reduce((acc, comic) => {
          comic.characters.forEach((character) => {
            const existing = acc.find((item) => item.name === character);
            if (existing) {
              existing.count++;
            } else {
              acc.push({ name: character, count: 1 });
            }
          });
          return acc;
        }, [])
        .sort((a, b) => a.name.localeCompare(b.name));
    },
    years() {
      let filteredComics = this.comics;
      if (this.selectedPublisher) {
        filteredComics = filteredComics.filter(
          (comic) => comic.publisher === this.selectedPublisher
        );
      }
      if (this.selectedArtist) {
        filteredComics = filteredComics.filter(
          (comic) => comic.artists && comic.artists.includes(this.selectedArtist)
        );
      }
      if (this.selectedCharacter) {
        filteredComics = filteredComics.filter(
          (comic) =>
            comic.characters && comic.characters.includes(this.selectedCharacter)
        );
      }
      return filteredComics
        .map((comic) => comic.year)
        .filter((year) => year != null)
        .reduce((acc, year) => {
          const yearString = String(year);
          const existing = acc.find((item) => item.name === yearString);
          if (existing) {
            existing.count++;
          } else {
            acc.push({ name: yearString, count: 1 });
          }
          return acc;
        }, [])
        .sort((a, b) => a.name.localeCompare(b.name));
    },
    artists() {
      let filteredComics = this.comics;
      if (this.selectedPublisher) {
        filteredComics = filteredComics.filter(
          (comic) => comic.publisher === this.selectedPublisher
        );
      }
      if (this.selectedCharacter) {
        filteredComics = filteredComics.filter(
          (comic) =>
            comic.characters && comic.characters.includes(this.selectedCharacter)
        );
      }
      if (this.selectedYear) {
        filteredComics = filteredComics.filter(
          (comic) => String(comic.year) === this.selectedYear
        );
      }
      const allArtists = filteredComics.reduce((acc, comic) => {
        if (comic.artists && Array.isArray(comic.artists)) {
          return acc.concat(comic.artists);
        }
        return acc;
      }, []);
      return allArtists
        .filter((artist) => artist != null)
        .reduce((acc, artist) => {
          const artistString = String(artist);
          const existing = acc.find((item) => item.name === artistString);
          if (existing) {
            existing.count++;
          } else {
            acc.push({ name: artistString, count: 1 });
          }
          return acc;
        }, [])
        .sort((a, b) => a.name.localeCompare(b.name));
    },
    filteredComics() {
      let filtered = this.comics;

      if (this.selectedPublisher) {
        filtered = filtered.filter(
          (comic) => comic.publisher === this.selectedPublisher
        );
      }

      if (this.selectedArtist) {
        filtered = filtered.filter(
          (comic) => comic.artists && comic.artists.includes(this.selectedArtist)
        );
      }

      if (this.selectedCharacter) {
        filtered = filtered.filter(
          (comic) =>
            comic.characters && comic.characters.includes(this.selectedCharacter)
        );
      }

      if (this.selectedYear) {
        filtered = filtered.filter(
          (comic) => String(comic.year) === this.selectedYear
        );
      }

      // Поиск
      if (this.isSearchApplied && this.searchQuery.trim()) {
        const query = this.searchQuery.toLowerCase();
        filtered = filtered.filter(comic => {
          const field = comic[this.searchField];
          return field && field.toLowerCase().includes(query);
        });
       this.isSearchAppliedClass = true;
      }

      return filtered;
    },
    sortedAndFilteredComics() {
      let sortedComics = [...this.filteredComics];

      if (this.sortField) {
        sortedComics.sort((a, b) => {
          const fieldA = a[this.sortField];
          const fieldB = b[this.sortField];
          if (fieldA < fieldB) return this.sortOrder === "asc" ? -1 : 1;
          if (fieldA > fieldB) return this.sortOrder === "asc" ? 1 : -1;
          return 0;
        });
      }

      return sortedComics;
    },
    yearSortArrow() {
      if (this.sortField === "year") {
        return this.sortOrder === "asc" ? "&#8595;" : "&#8593;";
      }
      return "";
    },
    ratingSortArrow() {
      if (this.sortField === "rating") {
        return this.sortOrder === "asc" ? "&#8595;" : "&#8593;";
      }
      return "";
    },
  },
  watch: {
    selectedPublisher(newVal) {
      this.selectedCharacter = "";
      this.selectedYear = "";
      this.selectedArtist = "";
    },
    selectedArtist(newVal) {
      this.selectedCharacter = "";
      this.selectedYear = "";
    },
    searchQuery(newVal) {
      // При изменении строки поиска сбрасываем флаг
      if(this.isSearchApplied) {
        this.isSearchApplied = false;
      }      
    },
  },
  methods: {
    async fetchComics() {
      try {
        // const response = await axios.get("http://localhost:3000/comics");
        // this.comics = response.data;

        const response = await fetch("db.json");
        const data = await response.json();
        console.log(data.comics);
        this.comics = data.comics;
      } catch (error) {
        console.error("Ошибка при загрузке комиксов:", error);
        console.warn(
          "Вероятнее всего нужно включить JSON сервер: 'json-server --watch db.json --port 3000'"
        );
      }
    },
    goToPreviousPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
        this.currentPageInput = this.currentPage;
      }
    },
    goToNextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        this.currentPageInput = this.currentPage;
      }
    },
    goToPageFromInput() {
      const pageNumber = parseInt(this.currentPageInput);
      if (isNaN(pageNumber) || pageNumber < 1) {
        this.currentPage = 1;
      } else if (pageNumber > this.totalPages) {
        this.currentPage = this.totalPages;
      } else {
        this.currentPage = pageNumber;
      }
      this.currentPageInput = this.currentPage;
    },
    toggleSort(field) {
      if (this.sortField === field) {
        if (this.sortOrder === null) {
          this.sortOrder = "desc";
        } else if (this.sortOrder === "desc") {
          this.sortOrder = "asc";
        } else {
          this.sortOrder = null;
          this.sortField = null;
        }
      } else {
        this.sortField = field;
        this.sortOrder = "desc";
      }
      this.currentPage = 1;
      this.currentPageInput = 1;
    },
    resetSort() {
      this.sortField = null;
      this.sortOrder = null;
      this.currentPage = 1;
      this.currentPageInput = 1;
    },
    applySearch() {
      this.currentPage = 1;
      this.currentPageInput = 1;
      this.isSearchApplied = true; 
    },
  },
};
</script>

