<template>
  <div class="comic-detail">
    <router-link to="/">
      &larr; Назад к списку
    </router-link>
    <div v-if="comic">  <!-- Добавляем v-if -->
      <h2>{{ comic.title }}</h2>
      <p v-if="comic.title_ru">**Название (рус):** {{ comic.title_ru }}</p>
      <div class="comic-info">
        <img class="comic-info__main-pic" :src="comic.coverImage" :alt="comic.title" width="200" />
        <div class="comic-details">
          <p>Издательство: {{ comic.publisher }}</p>
          <p>Год: {{ comic.year }}</p>
          <p>Жанр: {{ comic.genre }}</p>
          <p>Статус: {{ comic.status }}</p>
          <p>Рейтинг: {{ comic.rating }} / 100</p>
          <p>Рейтинг рисовки: {{ comic.artRating }} / 100</p>
          <p v-if="comic.startDate">Дата начала: {{ formatDate(comic.startDate) }}</p>
          <p v-if="comic.endDate">Дата завершения: {{ formatDate(comic.endDate) }}</p>
          <p v-if="comic.volume">Том: {{ comic.volume }}</p>
          <p v-if="comic.issues">Выпусков: {{ comic.issues }}</p>
          <p v-if="comic.readTo">Прочитано до: {{ comic.readTo }}</p>
          <p v-if="comic.event">Событие: {{ comic.event }}</p>
          <p v-if="comic.characters && comic.characters.length > 0">Персонажи: {{ comic.characters.join(', ') }}</p>
          <p v-if="comic.artists && comic.artists.length > 0">Художники: {{ comic.artists.join(', ') }}</p>
          <div v-if="comic.summary" >
            <p  v-html="comic.summary"></p>
          </div>
          <p v-if="comic.notes">Заметки: {{ comic.notes }}</p>
          <p v-if="comic.link">
            <a :href="comic.link" target="_blank">Читать комикс</a>
          </p>

          <div v-if="comic.additionalImages && comic.additionalImages.length > 0" class="additional-images">
            <h4>Дополнительные изображения:</h4>
            <!-- <img v-for="(image, index) in comic.additionalImages" :key="index" :src="image" :alt="'Доп. фото ' + (index + 1)" width="100" style="margin-right: 5px" /> -->

            <div :id="galleryID" class="comic-card__gallery" v-if="comic.additionalImages.length > 0">
              <a
                v-for="(image, index) in comic.additionalImages"
                :key="index"
                :href="image"
                target="_blank"
                rel="noreferrer"
              >
                <img :src="image" alt="Доп. фото" width="100" style="margin-right: 5px; cursor: pointer" />
              </a>
            </div>


          </div>
        </div>
      </div>
 
    </div>  <!-- Закрываем v-if -->
    <p v-else>Загрузка...</p>  <!-- Добавляем сообщение о загрузке -->
  </div>
</template>

<script>
import axios from "axios";
import { format } from 'date-fns'; // Import date-fns
import PhotoSwipeLightbox from 'photoswipe/lightbox';
import 'photoswipe/style.css';

export default {
  name: "ComicDetail",
  data() {
    return {
      comic: null,
      galleryID: `gallery-${Math.random().toString(36).substring(2, 15)}`,
      lightbox: null,
    };
  },
  mounted() {
    this.fetchComic();
  },
  beforeUnmount() {
    if (this.lightbox) {
      this.lightbox.destroy();
      this.lightbox = null;
    }
  },
  methods: {


    async getImageSize(imageUrl) {
      return new Promise((resolve) => {
        const img = new Image();
        img.onload = () => {
          resolve({ w: img.width, h: img.height });
        };
        img.onerror = () => {
          resolve({ w: 800, h: 600 }); // Размер по умолчанию при ошибке
        };
        img.src = imageUrl;
      });
    },

    async handleAdditionalImagesChange(event) {
      const files = event.target.files;

      if (files && files.length > 0) {
        this.form.additionalImages = [];

        for (let i = 0; i < files.length; i++) {
          const reader = new FileReader();
          reader.onload = (e) => {
            this.form.additionalImages.push(e.target.result);
          };
          reader.readAsDataURL(files[i]);
        }

        // Пересоздаем галерею после добавления новых картинок
        await this.$nextTick();

        if (this.lightbox) {
          this.lightbox.destroy();
        }

        this.lightbox = new PhotoSwipeLightbox({
          gallery: '#' + this.galleryID,
          children: 'a',
          pswpModule: () => import('photoswipe'),
          loop: true,
          zoomEl: true,
          pinchToClose: true,
          closeOnScroll: false,
          captionEl: true,
          counterEl: true,
        });

        this.lightbox.init();
      }
    },

    async fetchComic() {

      function getComicById(id, comics) {
        return comics.find(comic => comic.id === id);
      }



      try {
        const comicId = this.$route.params.id;

        const response1 = await fetch("../db.json");
        const data = await response1.json();  

        // const response = await axios.get(
        //   `http://localhost:3000/comics/${comicId}`
        // );
        this.comic = getComicById(comicId, data.comics);

        

// Дожидаемся, пока Vue обновит DOM
await this.$nextTick();

        if (this.comic.additionalImages.length > 0) {
          console.log('PhotoSwipeLightbox');
      this.lightbox = new PhotoSwipeLightbox({
        gallery: '#' + this.galleryID,
        children: 'a',
        pswpModule: () => import('photoswipe'),
        loop: true,
        zoomTitle: 'Zoom',
        zoomEl: true,
        wheelToZoom: true,
        pinchToClose: true,
        closeOnScroll: false,
        closeOnVerticalDrag: true,
        bgOpacity: 1,
        preventSwiping: false,
        showAnimationDuration: 0,
        hideAnimationDuration: 0,
        animateTransitions: true,
        closeEl: true,
        captionEl: true,
        fullscreenEl: true,
        shareEl: true,
        downloadEl: true,
        counterEl: true,
        arrowEl: true,
        preloaderEl: true,
        closeOnOutsideClick: false,
        tapToClose: false,
        tapToToggleControls: false,
      });

      this.lightbox.init();
    }


      } catch (error) {
        console.error("Ошибка при загрузке комикса:", error);
        // Обработка ошибки (например, отображение сообщения об ошибке)
        this.$router.push("/");
      }
    },
    editComic() {
      // Перенаправление на страницу редактирования
      this.$router.push({ name: 'edit-comic', params: { id: this.comic.id } });
    },
    formatDate(dateString) {
      if (!dateString) return '';
      try {
          const date = new Date(dateString);
          return format(date, 'yyyy-MM-dd'); // Change format as needed
      } catch (error) {
          console.error("Ошибка при форматировании даты:", error);
          return dateString; // Return original string if formatting fails
      }
    }
  },
};
</script>

<style scoped>
.comic-detail {
  padding: 20px;
}

.comic-info {
  display: flex;
  margin-bottom: 20px;
}

.comic-info__main-pic {
  flex: 0 0 470px;
    object-fit: cover;
}

.comic-details {
  margin-left: 20px;
}

.additional-images {
  margin-top: 10px;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}
@media (max-width: 400px) {
  main {
    padding: 10px 0;
  }
  .comic-info {
    flex-direction: column;
  }
  .comic-details {
    margin-left: 0;
}
.comic-info__main-pic{
  flex: 0 0 100%;
}
}
