import { createRouter, createWebHistory } from 'vue-router'
import ComicList from '../components/ComicList.vue'
import ComicDetail from '../components/ComicDetail.vue'
import ComicForm from '../components/ComicForm.vue'
import ComicEdit from '../components/ComicEdit.vue' // Import ComicEdit

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes: [
    {
      path: '/',
      name: 'home',
      component: ComicList
    },
    {
      path: '/comic/:id',
      name: 'comic-detail',
      component: ComicDetail
    },
    {
      path: '/add',
      name: 'add-comic',
      component: ComicForm
    },
    {
      path: '/comic/:id/edit', // Add route for ComicEdit
      name: 'edit-comic',
      component: ComicEdit,
      props: true
    }
  ]
})

export default router
