<template>
  <div class="comic-edit">
    <router-link :to="{ name: 'comic-detail', params: { id: id } }">
      &larr; Назад к деталям
    </router-link>
    <h2>Редактировать комикс</h2>
    <div v-if="comic"> <!-- Добавляем v-if -->
      <comic-form :comic="comic" @comic-updated="onComicUpdated" />
    </div>
    <p v-else>Загрузка...</p> <!-- Добавляем сообщение о загрузке -->
  </div>
</template>

<script>
import ComicForm from "./ComicForm.vue";
import axios from "axios";

export default {
  name: "ComicEdit",
  components: {
    ComicForm,
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      comic: null,
    };
  },
  mounted() {
    this.fetchComic();
  },
  methods: {
    async fetchComic() {
      try {
        const response = await axios.get(`http://localhost:3000/comics/${this.id}`);
        this.comic = response.data;
      } catch (error) {
        console.error("Ошибка при загрузке комикса:", error);
        // Обработка ошибки (например, перенаправление на страницу со списком комиксов)
        this.$router.push("/");
      }
    },
    onComicUpdated() {
      // Перенаправление на страницу детальной информации о комиксе после успешного редактирования
      this.$router.push({ name: 'comic-detail', params: { id: this.id } });
    },
  },
};
</script>

<style scoped>
.comic-edit {
  padding: 20px;
}
</style>
